export type TasksType = {
  [key: string]: (...args: Array<any>) => any;
};
export type StatusType = {
  [key: string]: {
    count: number; // è¿ç»­éè¯¯æ¬¡æ°
    status: "pending" | "fulfilled" | "rejected";
  };
};

class Polling {
  constructor(interval = 1000) {
    // ä»»å¡å¯¹å
    this.#tasks = {};
    this.#status = {};
    this.#interval = interval;
  }

  #tasks: TasksType;
  #status: StatusType;
  #timer: any;
  #interval: number;

  #setStatus(key, status) {
    let obj = this.#status[key];
    if (!obj) {
      obj = {
        count: 0,
        status,
      };
    }
    obj.status = status;
    if (status === "rejected") {
      obj.count += 1;
    } else if (status === "fulfilled") {
      // æåä¸æ¬¡å°±æ¸é¶
      obj.count = 0;
    }
    if (obj.count >= 3) {
      this.del(key);
    }
    this.#status[key] = obj;
  }

  start() {
    if (this.#timer) {
      throw "polling has already started";
    }
    this.#timer = setInterval(() => {
      for (const [key, fn] of Object.entries(this.#tasks)) {
        if (["AsyncFunction"].includes(fn.constructor.name)) {
          // å¦æä¸ä¸ä¸ªå¼æ­¥æ¹æ³è¿å¨æ§è¡ï¼åæ¬æ¬¡ä¸æ§è¡
          if (this.#status[key]) {
            if (this.#status[key].status === "pending") {
              return;
            }
          }
          // å¼æ­¥æ¹æ³
          this.#setStatus(key, "pending");
          fn()
            .then(() => {
              this.#setStatus(key, "fulfilled");
            })
            .catch((e) => {
              this.#setStatus(key, "rejected");
            });
        } else {
          // åæ­¥æ¹æ³
          fn();
        }
      }
    }, this.#interval);
  }
  add(key: string, fn: (...args: Array<any>) => any, ...args: Array<any>) {
    if (this.#tasks[key]) {
      console.error(`[polling]: key ${key} å·²ç»å­å¨`);
    } else {
      if (typeof fn === "function") {
        if (args.length) {
          this.#tasks[key] = fn.bind(null, ...args);
        } else {
          this.#tasks[key] = fn;
        }
      } else {
        console.error(`[polling]: ${fn} ä¸æ¯ä¸ä¸ªå½æ°`);
      }
    }
  }
  del(key?: string) {
    if (key) {
      delete this.#tasks[key];
    } else {
      this.#tasks = {};
      this.#status = {};
    }
  }
  stop() {
    if (this.#timer) {
      clearInterval(this.#timer);
      this.#timer = null;
    }
  }
}

export default Polling;
